import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { Heroe } from '../interfaces/heroe.interface';

@Injectable({
  providedIn: 'root'
})
export class HeroesService {

  private protocol = 'https:';
  private ApiUrl = '//gateway.marvel.com:443/v1/public/';
  private APIKEY = '56d2cc44b1c84eb7c6c9673565a9eb4b';
  public heroes: Observable<Heroe[]>;
  public page = 0;
  public step = 20;
  public total = 0;
  public group_colors = {
    "azul" : "#1f8ff7",
    "violeta":"#a43de3",
    "naranjo":"#df5c0f",
    "verde":"#0ea521"
  }
  public teams = new Map();

  constructor(private http: HttpClient) { }

  getHeroe(id) {
    const url = `${this.protocol}${this.ApiUrl}characters/${id}?apikey=${this.APIKEY}`;
    return this.http.get<any>(url);
  }

  getHeroes(nameStartsWith?: string, page?: number): Observable<any> {
    if (page || page === 0) {
      this.page = page;
    }
    return this.getHeroesRequest(nameStartsWith, page).pipe(
      map(data => {
        this.total = Math.ceil(data.data.total / this.step);
        return data.data.results.map((result: Heroe) => {
          return {
            id: result.id,
            name: result.name,
            description: result.description,
            modified: result.modified,
            thumbnail: result.thumbnail,
            resourceURI: result.resourceURI,
            teamColor: this.getTeamColor(result.id)
          }
        });
      })
    )
  }

  getPage() {
    return this.page;
  }

  getTotalPages() {
    return this.total
  }

  getHeroesRequest(nameStartsWith?: string, page?: number): Observable<any> {
    const url = `${this.protocol}${this.ApiUrl}characters?apikey=${this.APIKEY}&offset=${this.page * this.step}${nameStartsWith ? ('&nameStartsWith=' + nameStartsWith) : ''}`;
    return this.http.get<any>(url);
  }

  getHeroesArr(): Observable<any> {
    return this.heroes;
  }

  resetPager() {
    this.page = 0;
  }

  getTeamColor(id):string{
    if(this.teams.get(id)!=undefined){
    return this.teams.get(id);
    }
    else{
    return "";
    }
  }
}
