import { Component, OnInit } from '@angular/core';
import { Heroe } from '../shared/interfaces/heroe.interface';
import { HeroesService } from '../shared/services/heroes.service';



@Component({
  selector: 'app-listado-de-heroes',
  templateUrl: './listado-de-heroes.component.html',
  styleUrls: ['./listado-de-heroes.component.scss']
})
export class ListadoDeHeroesComponent implements OnInit {

  public title = 'Tutorial de Angular - Héroes de Marvel';
  public searchString : string;
  public heroes: Heroe[] = [];
  public page: number = 0;
  public totalPages: number = 0;

  constructor(private heroesService: HeroesService) { }

  ngOnInit() {
    this.getHeroes();
  }

  submitSearch() {
    this.getHeroes(this.searchString)
  }

  prevPage() {
    this.getHeroes(this.searchString, this.page - 1);
  }

  nextPage() {
    this.getHeroes(this.searchString, this.page + 1);
  }

  getHeroes(searchHeroe?: string, page?: number) {
    this.heroesService.getHeroes(searchHeroe, page).subscribe( data => {
      console.log(data)
      this.heroes = data;
      this.page = this.heroesService.page;
      this.totalPages = this.heroesService.total;
    })
  }

}
